<script>   

	import { formatThousands } from '@/main';

    export default {
        name: 'ShopComponent',

        mounted() { 
            window.ShopComponent = this;     

			if(window.mp) {
                window.e_rpc.add('browser::premium:update', (data) => {
                    const raw = JSON.parse(data)
 
                    this.premiumpoints = raw.premiumpoints;
                }); 
            } 
        },

        data: function() {
            return {  
				premiumpoints: 0,
				items: [
					{
						name: '10.000$',
						image: 'https://i.imgur.com/jrf6KRS.png',
						amount: 20,
						option: 'money',
						received: 10000
					}
				]

            };
        },    
 
        methods: { 
			formatThousands,

			open(data) { 
				this.premiumpoints = data.premiumpoints;
			},
			
			purchaseItem(item) {
				if(window.mp) 
					return window.e_rpc('server::premium:purhcase', JSON.stringify(item)); 
			},

			closeMenu() {
				if(window.mp) 
                    return window.e_trigger('client::premium:close'); 
			}
		}
    }  
</script>

<template>  
    <div class="absolute w-full h-full flex justify-center items-center z-1">
		<div class="absolute w-full h-full z-[-1] bg-gray-900/70 backdrop-blur-md"></div> 

        <div class="flex flex-col justify-center items-start w-[53vw] h-[38vw] rounded-lg bg-gray-900 text-white">
 
			<div class="flex justify-between items-center w-full h-[10%] border-b border-b-gray-400/30 p-3">
				
				<div class="flex flex-col justify-center items-start w-[50%] h-full">
					<div class="font-montserrat font-bold text-[1vw] tracking-wide text-white/90"><i class="fa-brands fa-shopify"></i> PREMIUM SHOP</div>
					<div class="font-inter font-semibold text-[0.6vw]  "><span class="text-white/50">You have</span>  <span class="text-yellow-500 font-bold"><i class="fa-solid fa-coins"></i> {{ this.formatThousands(this.premiumpoints) }}</span> <span class="text-white/50">premium points</span></div> 
				</div>
				

				<div class="flex flex-col justify-center items-end w-[50%] h-full"> 
					<button class="flex justify-center items-center w-[2.3vw] h-[2.3vw] rounded bg-gray-800/50 text-white/50 transition duration-300 hover:bg-rose-600 hover:text-white" @click="this.closeMenu()"><i class="fa-solid fa-xmark text-[1.2vw]"></i></button>
				</div> 
			</div> 

			<div class="flex w-full h-[90%] justify-start items-center ">
 
				<div class="flex-col w-full h-full grid grid-cols-5 gap-[0.50vw] gap-x-[2] max-h-[31.5vw] overflow-y-scroll content-start p-2">
					<div class="w-[10vw] h-[10vw] bg-gradient-to-b from-gray-800/50 to-gray-800/30 rounded-md flex flex-col items-center justify-center transition-all duration-500 " v-for="(item) in this.items" :key="item"> 


						<img :src="item.image" width="90">
						<div class="font-onest font-semibold text-[1vw] tracking-wide text-white/80">{{ item.name }}</div>

						<div @click="this.purchaseItem(item)" class="bg-yellow-500/30 rounded-full mt-3 flex justify-center items-center w-[90%] h-[1.7vw] font-medium text-yellow-400 text-theme-xs transition duration-500 cursor-pointer hover:bg-yellow-500/50 hover:text-yellow-300">
							<i class="fa-solid fa-check-double mr-1"></i> <span class="text-[0.6vw] tracking-wider">Purchase for {{ item.amount }}<i class="fa-solid fa-coins ml-[1px]"></i> </span> 
						</div>
					</div>
				</div>
			</div> 
		</div> 
    </div> 
</template>

